import { DisplayFinalProductPage } from './../DisplayFinalProduct/DisplayFinalProduct';
import { ProductService } from '../../services/product';
import { FinalProduct } from './../../models/FinalProduct.interface';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'page-AddFinalProduct',
  templateUrl: 'AddFinalProduct.html'
})
export class AddFinalProductPage {

  product = {} as FinalProduct;
  private productArray:FinalProduct[];

  constructor(public navCtrl: NavController, private productDatabase: ProductService) {

  }

  addProduct(product:FinalProduct) {
    //the product is not coming from the form.
    this.productDatabase.addProduct(product);
  }

  goToList(product: FinalProduct){
    this.navCtrl.push(DisplayFinalProductPage);
  }

}



