import { AlertController } from 'ionic-angular';
import firebase from 'firebase';
import {Injectable} from '@angular/core';

@Injectable()
export class AuthService {

  constructor(public alertCtrl: AlertController){

  }

    signup(email: string, password: string) {
        return firebase.auth().createUserWithEmailAndPassword(email, password);
       //return this.afAuth.auth.createUserWithEmailAndPassword(email, password);
    }

    facebook_signin(fc:any) {
        //return firebase.auth().signInWithEmailAndPassword(email, password);
        const alert = this.alertCtrl.create({
          title: 'Inide Auth Facebook Login',
          buttons: ['Dismiss']
        });
    
        alert.present();

        firebase.auth().signInWithCredential(fc).then(
            function (fs){
              //alert('firebase accessed');
            }
          ).catch(
            function(f_err){
              //alert('firebased error');
            }
          );
    }

    getActiveUser(){
      //This is returning a user object
      console.log("Firebase UId:" + firebase.auth().currentUser);
        return firebase.auth().currentUser;
    }

}


